import vue, {the, lib, library} from "vue.js"
import plugin, {ip_address} from "@/plugin"
let {define} = lib
let {not, my_self} = lib.function
let {zero, one} = lib.number

const {STATUS_OK} = lib.constant
const {ERROR, ERROR_BAD_REQUEST, ERROR_FORBIDDEN, ERROR_FOUND, ERROR_REQUEST_TIMEOUT, ERROR_CONFLICT, ERROR_LENGTH} = lib.constant

