if (null) {
	the.socket = new lib.web.socket.io ()
	the.socket.url = lib.dom.attribute ("meta[name=socket:url]", "content")
	the.socket.token = lib.dom.attribute ("meta[name=socket:token]", "content")
	the.socket.start ()
	the.socket.emit ("connect").next (function (response) {
		if (response) console.log ("socket connect", response.id)
		})
	the.socket.on ("close", function (response) {
		console.log ("socket disconnect", response)
		})
	}

