<script>
import vue, {the, lib, library} from "vue.js"
export default vue.component ({
	id: "media-query:line",
	attribute: [],
	setup () {
		var data = vue.reference ("on")
		vue.watch (vue.on.line (vue.reference), function (on) {
			if (on) data.value = "on"
			else data.value = "off"
			})
		return vue.data ({data})
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		on () { return this.data === "on" },
		},
	})
</script>

<template id="media-query:line" type="component">
	<div v-if="on ()" id="media-query:line" v-bind:aria-data="data">
		<slot v-if="slot ()"/>
		<section v-else>
			on:line
		</section>
	</div>
	<div id="media-query:line" template="pop" v-bind:aria-data="data" v-else>
		<slot v-if="slot ()"/>
		<section v-else>
			off:line
		</section>
	</div>
</template>

<style type="text/css">
[id="media-query:line"][aria-data="on"] { display: none; background-color: rgba(var(--background-color), 1); }
</style>
