the.app = function () { the.app.context (... arguments) }
the.app.json = {... vue.ini ("app")}

define (the.app).set ("name", the.app.json ["name"])
define (the.app).set ("description", the.app.json ["description"])
define (the.app).set ("version", the.app.json ["version"])
define (the.app).set ("host", the.app.json ["host"])
define (the.app).set ("url", the.app.json ["url"])
define (the.app).set ("email", the.app.json ["email"])
define (the.app).set ("phone", the.app.json ["phone"])
define (the.app).set ("logo", the.app.json ["logo"])
define (the.app).set ("status", the.app.json ["status"])
define (the.app).set ("development", the.app.json ["development"])
define (the.app).set ("config", the.app.json ["config"])

/**
 * xxx
 *
 * title
 * description
 * sub description
 *
 * xxx://xxx.xxx.xxx/xxx
 */

the.ref = {}
the.reference = function (key) { return the.ref [key] }
the.var = function (key, value) {
	if (key) {
		if (arguments.length > one) return the.ref [key] = vue.reference (value)
		return the.ref [key].value
		}
	return the.ref
	}

the.react = vue.reactive ()
the.reactive = function (key, value) {
	if (lib.is.object (key)) vue.reactive (the.react, key)
	else if (key) if (arguments.length > one) return the.react [key] = value
	else return the.react [key]
	else return the.react
	}
the.reactive.toggle = function (key) { the.react [key] = ! the.react [key] }
the.reactive.delete = function (key) { delete the.react [key] }
the.reactive.delete.truncate = the.reactive.clear = function () { for (var key in the.react) delete the.react [key] }

/**
 * xxx
 *
 * title
 * description
 * sub description
 *
 * xxx://xxx.xxx.xxx/xxx
 */

the.event = {}

the.on = function (key, value) {
	if (the.event [key]) return the.event [key].push (value)
	else return the.event [key] = [value]
	}

the.emit = function (key, ... value) {
	var emit
	for (var i in the.event [key]) emit = the.event [key][i] (... value)
	return emit
	}

