var test = vue.ini ("test")
vue.test = function () { return test }

var debug = vue.ini ("debug")
vue.debug = vue.log.console = function (... output) { if (debug) console.log (... output) }

var progress = vue.ini ("progress")
for (var i in progress) vue.on.progress.insert (progress [i])

