<script>
import vue, {lib, library} from "vue.js"
export default vue.element ({
	attribute: ["size", "background", "color"],
	setup (prop) {
		return {vue, loader}
		},
	})
var loader = {
	size: function (px) { return ("width: %spx; height: %spx;").format (px || 50) },
	background: function (color) { return color || "#eeeeee" },
	color: function (color) { return color || "#888888" },
	}
</script>

<template id="loader:circle" type="element">
	<div class="flex-line" v-bind:style="loader.size (size)">
		<svg viewBox="0 0 24 24" class="circle" v-bind:style="loader.size (size)">
			<circle cx="12" cy="12" r="9.5" fill="none" stroke-width="0.5" v-bind:stroke="loader.background (background)"/>
			<circle cx="12" cy="12" r="9.5" fill="none" stroke-width="0.5" class="animation" v-bind:stroke="loader.color (color)"/>
		</svg>
	</div>
</template>

<style scoped>
.circle { transform-origin: center; animation: rotate 2s linear infinite; }
.animation { stroke-linecap: round; animation: dash 1.5s ease-in-out infinite; }
@keyframes rotate { 100% { transform: rotate(1turn); } }
@keyframes dash {
	0% { stroke-dasharray: 0 150; stroke-dashoffset: 0; }
	50% { stroke-dasharray: 42 150; stroke-dashoffset: -16; }
	100% { stroke-dasharray: 42 150; stroke-dashoffset: -59; }
	}
</style>
