<script>
import vue, {lib, library} from "vue.js"
import sign_up from "@/asset/image/illustration/draw/vector/sign_up.svg"
export default vue.next ({
	element: "img:draw",
	attribute: ["src", "slot"],
	setup (prop) {
		var img = {
			sign_up,
			}
		var src = vue.reference (prop.src)
		src.value = img [src.value]
		return {vue, src}
		},
	})
</script>

<template id="img:draw" type="element">
	<img v-bind:src="src"/>
</template>
