<script>
import vue, {the, lib, library} from "vue.js"
export default vue.next ({
	id: "vue",
	setup () {
		vue.start ()
		return vue.export ()
		},
	mount () {
		if (vue.ini ("state:watch")) {
			vue.watch (vue.state (), function (state) {
				if (true) vue.emit ("state", state)
				else vue.state (function (state) { vue.emit ("state", state) })
				})
			}
		},
	watch: {
		$route () {
			vue.parse_url ()
			vue.route ()
			},
		},
	method: {
		is_audio () { return vue.setting ("media-query:audio") },
		is_screen () { return vue.setting ("media-query:screen") },
		is_splash () { return vue.setting ("media-query:splash") },
		is_line () { return vue.setting ("media-query:line") },
		is_loading () { return vue.setting ("media-query:loading") },
		is_progress () { if (vue.setting ("media-query:progress")) return vue.on.progress () },
		},
	})
</script>

<template>
	<media-player:audio v-if="is_audio ()"/>
	<media-query:screen v-if="is_screen ()"/>
	<media-query:splash v-if="is_splash ()"/>
	<media-query:line v-if="is_line ()"/>
	<media-query:loading v-if="is_loading ()"/>
	<media-query:progress v-if="is_progress ()"/>
	<router-view v-else-if="vue.system.setup" id="application"/>
	<div v-else></div>
</template>
