<script>
import vue, {the, lib, library} from "vue.js"
export default vue.component ({
	id: "media-query:splash",
	attribute: [],
	setup () {
		var data = vue.reference ()
		vue.watch (vue.media.query.splash.status, function (on) {
			if (on) data.value = "on"
			else data.value = "off"
			})
		return vue.data ({data})
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		},
	})
</script>

<template id="media-query:splash" type="component">
	<div v-if="slot ()" id="media-query:splash" template="pop" v-bind:aria-data="data">
		<slot slot/>
	</div>
	<div id="media-query:splash" template="pop" v-bind:aria-data="data" v-else>
		splash
	</div>
</template>

<style type="text/css">
[id="media-query:splash"] { display: none; }
[id="media-query:splash"][aria-data="on"] { display: none; }
</style>
