import vue, {lib, library} from "vue.js"
var router = []
router.push ({path: "/", name: "/", component: () => import ("./link/index.vue"), meta: {}})
router.push ({path: "/journal.html", name: "app:journal", component: () => import ("./link/journal.vue"), meta: {}})
router.push ({path: "/test", name: "test", component: () => import ("./link/test.vue"), meta: {}})
router.push ({path: lib.router.try.catch, name: "*", component: () => import ("./slot/index.vue"), meta: {}})
export default router = lib.router.create ({history: lib.router.history (vue.request.url), [("route").concat ("s")]: router})