<script>
import vue, {the, lib, library} from "vue.js"
export default vue.component ({
	attribute: [],
	setup () {
		var data = vue.reference ("on")
		console.log (vue.on.progress.data)
		vue.watch (vue.on.progress (vue.reference), function (progress) {
			if (progress.length) data.value = "on"
			else data.value = "off"
			})
		return vue.data ({data})
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		clear () { if (null) vue.on.loading.delete.truncate () },
		},
	})
</script>

<template id="media-query:progress" type="component">
	<div v-if="slot ()" id="media-query:progress" template="pop" v-bind:aria-data="data">
		<slot slot/>
	</div>
	<div id="media-query:progress" template="pop" v-bind:aria-data="data" v-else>
		<div class="flex align-item justify-item view-port">
			<img:logo v-if="the.app.logo" v-bind:src="the.app.logo" class="aria-img"/>
			<img:logo v-else-if="vue.on.progress.image" v-bind:src="vue.on.progress.image" class="aria-img"/>
			<img:platform src="vue" class="aria-img" v-else/>
		</div>
	</div>
</template>

<style type="text/css">
[id="media-query:progress"] { background-color: rgba(var(--background-color), 1); }
[id="media-query:progress"][aria-data="on"] { display: block; }
[id="media-query:progress"][aria-data="off"] { display: none; }
[id="media-query:progress"] .aria-img { width: 144px; height: auto; }
</style>
