<script>
import vue, {the, lib, library} from "vue.js"
import lab_promo from "@/asset/image/lab_promo.gif"
export default vue.component ({
	attribute: ["icon"],
	setup () {
		var data = vue.reference ("off")
		vue.watch (vue.on.loading (vue.reference), {deep: true}, function (value) {
			if (vue.on.loading ()) data.value = "on"
			else data.value = "off"
			})
		return vue.data ({data, lab_promo})
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		clear () { if (null) vue.on.loading.delete.truncate () },
		},
	})
</script>

<template id="media-query:loading" type="component">
	<div v-if="slot ()" id="media-query:loading" template="pop" v-bind:aria-data="data">
		<slot slot/>
	</div>
	<div id="media-query:loading" template="pop" v-bind:aria-data="data" v-else>
		<div class="flex align-item justify-item view-port">
			<img v-if="true" v-bind:src="lab_promo" class="aria-img"/>
			<img:logo v-else-if="the.app.logo" v-bind:src="the.app.logo" class="aria-img"/>
			<img:logo v-else-if="vue.on.loading.image" v-bind:src="vue.on.loading.image" class="aria-img"/>
			<img:platform src="vue" class="aria-img" v-else/>
		</div>
	</div>
</template>

<style type="text/css">
[id="media-query:loading"] { background-color: rgba(var(--background-color), 1); }
[id="media-query:loading"][aria-data="on"] { display: block; }
[id="media-query:loading"][aria-data="off"] { display: none; }
[id="media-query:loading"] .aria-img { width: 144px; height: auto; }
</style>
