<script>
import vue, {the, lib, library} from "vue.js"
export default vue.component ({
	id: "redirect",
	attribute: ["url", "delay"],
	setup (prop) {
		lib.time.sleep (function () { vue.url.go (prop.url) }, (prop.delay || lib.number.zero))
		return vue.data ()
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		},
	})
</script>

<template id="redirect" type="component">
	<div v-if="slot ()"><slot slot/></div>
	<div class="view fixed z-index background-color" v-else></div>
</template>
